import Nav from "./Nav";
import About from "./About";
import Search from "./Search";
import Home from "./Home";
import {
Route,Switch
 
} from "react-router-dom";
function App() {
  return (
    <div className="App">
      <Nav/>
      <Switch>
        <Route path="/" exact component={Home}/>
        <Route path="/about" component={About}/>
        <Route path="/search" component={Search}/>

      </Switch>
    </div>
  );
}

export default App;
